<template>
  <div class="thiscomponent">
    <v-card
        class="mx-auto"
        max-width="100%"
    >
      <v-card-text>
        <div class="text--primary">{{ qindex }} 번 항목</div>
        <div class="text--primary">
          {{ qtext }}
        </div>
      </v-card-text>
      <v-card-actions>
        <v-radio-group v-model="scoreValue" v-on:change="scoreChanged($event)">
          <v-radio
              label="매우 (5점)"
              :value="5"
          ></v-radio>
          <v-radio
              label="대체로 (3점)"
              :value="3"
          ></v-radio>
          <v-radio
              label="조금 (1점)"
              :value="1"
          ></v-radio>
          <v-radio
              label="전혀 (0점)"
              :value="0"
          ></v-radio>
        </v-radio-group>
      </v-card-actions>
    </v-card>
  </div>
</template>
<style scoped>
.thiscomponent {
}
</style>
<script>
export default {
  name: 'QuestionItem',
  props: {
    qindex: Number,
    qtext: String,
    value: Number
  },
  data() {
    return {
      scoreValue: -1
    }
  },
  watch: {
    value() {
      this.scoreValue = this.value;
    }
  },
  methods: {
    scoreChanged(value) {
      this.$emit('input', value);
    }
  }
}
</script>
